.analyticsWrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
  margin-top: 45px;
}

.dividerContainer {
  position: relative;
  margin-top: 10px;
}

.transactionsLabel {
  position: absolute;
  top: -35px;
  left: -220px;
  font-size: 1rem;
  font-weight: 600;
  color: #111827;
  letter-spacing: -0.01em;
  font-family: var(--font-inter), sans-serif;
  z-index: 1;
}

.dividerLine {
  width: calc(100% + 220px);
  margin-left: -227px;
  margin-top: 1px;
}

.balanceCard {
  position: relative;
  width: 100%;
  min-width: 520px;
  min-height: 200px;
  background: transparent;
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: none;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  margin-top: 30px;
}

.balanceHeader {
  background-color: transparent;
  backdrop-filter: none;
  padding: 16px;
  padding-bottom: 8px;
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
}

.balanceLabel {
  font-size: 1rem;
  font-weight: 600;
  color: #111827;
  letter-spacing: -0.01em;
  font-family: var(--font-inter), sans-serif;
}

.balanceAmount {
  font-size: 2rem;
  font-weight: 700;
  color: #111827;
  margin-top: 10px;
  font-family: var(--font-inter), sans-serif;
  letter-spacing: -0.02em;
  line-height: 1.2;
}

.growth {
  font-size: 0.875rem;
  color: #2563eb;
  font-weight: 500;
}

.chartHidden {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.6s ease, transform 0.6s ease;
}

.chartVisible {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 0.6s ease, transform 0.6s ease;
}

/* МОБИЛЬНАЯ АДАПТАЦИЯ - ТОЛЬКО ДЛЯ МОБИЛЬНЫХ */
@media (max-width: 768px) {
  .analyticsWrapper {
    margin-top: 20px !important;
  }

  .dividerContainer {
    margin-top: 0 !important;
  }

  .transactionsLabel {
    position: static !important;
    left: 0 !important;
    top: 0 !important;
    margin-bottom: 8px;
  }

  .dividerLine {
    width: 100% !important;
    margin-left: 0 !important;
  }

  .balanceCard {
    min-width: 0 !important;
    min-height: 180px !important;
    margin-top: 16px !important;
  }

  .balanceAmount {
    font-size: 1.5rem !important;
  }

  /* Транзакции - убираем отрицательный отступ */
  .analyticsWrapper > :global([class*="mantine-Stack-root"]) {
    margin-left: 0 !important;
  }

  /* Иконки транзакций - уменьшаем размер */
  .analyticsWrapper div[style*="width: '40px'"],
  .analyticsWrapper div[style*="width: \"40px\""] {
    width: 32px !important;
    height: 32px !important;
  }

  /* Текст транзакций - уменьшаем размер */
  .analyticsWrapper :global([class*="mantine-Stack-root"]) :global([class*="mantine-Text-root"]) {
    font-size: 0.8rem !important;
  }
}