/* МОБИЛЬНАЯ АДАПТАЦИЯ - ТОЛЬКО ДЛЯ МОБИЛЬНЫХ */
@media (max-width: 768px) {
  /* Контейнер страницы */
  :global([class*="mantine-Container-root"]) {
    margin-right: 0 !important;
    padding-left: 12px !important;
    padding-right: 12px !important;
  }

  /* Контейнер с графиками - убираем grid-column, margin-left, border */
  :global([style*="grid-column: span 2"]) {
    grid-column: span 1 !important;
    margin-left: 0 !important;
    border-left: none !important;
    padding-left: 0 !important;
    flex-direction: column !important;
    align-items: stretch !important;
  }

  /* Обертка DonutChart - убираем margin-top */
  :global([style*="margin-top: 80px"]) {
    margin-top: 0 !important;
    margin-bottom: 20px !important;
  }
}
